@import url('https://fonts.googleapis.com/css2?family=Rubik+Mono+One&display=swap');

body {
  font-family: 'Rubik Mono One', sans-serif;
}

#root {
  display: flex;
  justify-content: center;
  align-items: center; 
  height: 100vh;
}

@media screen and (max-width: 650px) {
  #drum-machine {
    transform: scale(0.7);
    font-size: 1.4rem;
  }
}

#drum-machine {
  outline: 5px solid rgb(28, 32, 34);
  position: relative;
  width: 660px;
  text-align: center;
  background-color: #b3b3b3;

  font-size: 1.6rem;
}

#display {
  width: 332px;
  height: 272px;
  display: inline-block;
  margin: 20px;
}

.drum-pad {
  position: relative;
  float: left;
  width: 100px;
  height: 80px;
  margin-right: 10px;
  border-radius: 5px;
  padding-top: 35px;
  box-sizing: border-box;
  cursor: pointer;
}

